import { AlertCircle, CheckCircle2, Info, XCircle } from "lucide-react";
import { cn } from "@/lib/utils";

type ScodeType = "warning" | "info" | "success" | "destructive";
type ScodeSize = "sm" | "md" | "lg";

interface ScodeProps {
    type: ScodeType;
    size?: ScodeSize;
    children: React.ReactNode;
    className?: string;
}

const icons = {
    warning: AlertCircle,
    info: Info,
    success: CheckCircle2,
    destructive: XCircle,
};

const styles = {
    warning: "bg-yellow-500/10 text-yellow-500 border-yellow-500/20",
    info: "bg-blue-500/10 text-blue-500 border-blue-500/20",
    success: "bg-green-500/10 text-green-500 border-green-500/20",
    destructive: "bg-red-500/10 text-red-500 border-red-500/20",
};

export default function Scode({ type = "info", size = "md", children, className }: ScodeProps) {
    const Icon = icons[type];

    return (
        <div className={cn(
            "flex items-start gap-3 rounded-lg border p-4 my-4 font-mono text-sm leading-relaxed",
            styles[type],
            className
        )}>
            <Icon className="w-5 h-5 shrink-0 mt-0.5" />
            <div className="flex-1 min-w-0">
                {children}
            </div>
        </div>
    );
}
